import React from 'react'
import { connect } from 'react-redux'
import './IndicatorLegend.css'


let IndicatorLegend = ({ dispatch, indicators, point }) => {
  let active_indicators = indicators.filter(layer => layer.active === true);
  return (
    <div className="c-indicator-legend">
      {
        active_indicators.map((value, i) => (
          <div key={i}>
            <h3 className={"c-indicator-legend__heading c-indicator-legend__heading-" + active_indicators.length}>{ value.displayName }</h3>
            <img src={ value.source + "REQUEST=GetLegendGraphic&format=image/png&layer=" + value.params.layers } className="c-indicator-navigator__legend" id={ "indicator-navigator-legend-" + i } alt="" />
          </div>
        ))
      }
    </div>
  )
}

const mapStateToProps = state => {
  return {
    indicators: state.indicators.indicators
  }
}

const mapDispatchToProps = dispatch => {
  return {
    dispatch: dispatch
  }
}

IndicatorLegend = connect(mapStateToProps, mapDispatchToProps)(IndicatorLegend)

export default IndicatorLegend



